body {
  padding: 100px;
  background: #eee; }

.wrap {
  text-align: center;
  vertical-align: middle;
  margin-bottom: 100px;
  background: white;
  padding-bottom: 100px;
  box-shadow: 0px 40px 60px -20px rgba(0,0,0,0.2); }

h1 {
  padding: 50px; }

.spinner {
  width: 100px;
  height: 100px;
  background: #eee;
  border-radius: 50%;
  position: relative;
  margin: 50px;
  display: inline-block;

  &:after, &:before {
    content: '';
    display: block;
    width: 100px;
    height: 100px;
    border-radius: 50%; } }


.spinner-1:after {
  position: absolute;
  top: -4px;
  left: -4px;
  border: 4px solid transparent;
  border-top-color: orangered;
  border-bottom-color: orangered;
  animation: spinny 1s linear infinite; }

@keyframes spinny {
  0% {
    transform: rotate(0deg) scale(1); }
  50% {
    transform: rotate(45deg) scale(1.2); }
  100% {
    transform: rotate(360deg) scale(1); } }

.spinner-4 {
  &:before, &:after {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -50px;
    margin-left: -50px;
    background: black;
    animation: pulse 3s linear infinite;
    opacity: 0; }

  &:after {
    animation: pulse 2s linear 2.3s infinite; } }

@keyframes pulse {
  0% {
    transform: scale(0);
    opacity: 1; }
  100% {
    transform: scale(1.3);
    opacity: 0; } }



.spinner-5 {
  height: 0px;
  width: 0px; }
